.btn {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color:transparent;
  color:$red;
  border:1px solid $red;
  border-radius: 8px;
  height: 46px;
  padding: 13px 24px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  line-height: 21px;
  transition: background-color .3s, color .3s;
  text-align:center;
  &:hover{
    background-color: $red;
    color:#fff;
  }

  &--filled{
    background-color: $red;
    color:#fff;
    &:hover{
      background-color: transparent;
      color:$red;
    }
  }
}
