.header {
  position: fixed;
  left: 0;
  right: 0;
  top:0;
  height: 86px;
  z-index: 99;
  transition: background-color .3s;
  background: #2B2F35;
  @media (max-width: $mobileMedium){
    height: 60px;
  }
  &__logo{
    display: inline-block;
    @media (max-width: 1024px){
      margin-right: auto;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;
    padding: 20px 0;
  }

  &__nav {
    margin: 0 auto;
    @media (max-width: 1200px){
      font-size: 13px;
    }
    @media (max-width: 1100px){
      font-size: 12px;
    }
    @media (max-width: 1024px){
      display: none;
    }

    ul {
      display: flex;
      align-items: center;
      li + li{
        margin-left: 14px;
        @media (max-width: 1100px){
          margin-left: 8px;
        }
      }

      a {
        position: relative;
        display: inline-block;
        transition: color .3s;
        padding:5px;
        &:before {
          content: '';
          position: absolute;
          top: calc(100% + 5px);
          height: 1px;
          left: 50%;
          transform: translateX(-50%);
          background-color:$red;
          width: 0;
          transition: width .2s;
        }

        &:hover {
          color: $red;

          &::before{
            width: calc(100% + 4px);
          }
        }
      }
    }
  }

  &__book-btn {
    margin-left: 30px;
    @media (max-width: 1100px){
      margin-left: 20px;
    }
    @media (max-width: $mobileMedium){
      display: none;
    }
  }

  &__tel {
    white-space: nowrap;
    a {
      @include hover-effect;
    }
    @media (max-width: $mobileMedium){
      display: none;
    }
  }
}
