@import "fonts";
@import "null";
@import "mixins";
@import "./vendors/swiper";
@import "./vendors/air-datepicker";
@import "./vendors/choices";

$red: #DC493A;
$laptop: 1240px;
$tablet: 991.92px;
$mobile: 767.92px;
$mobileMedium: 575.98px;
@import "forms";
@import "typegr";
@import "btns";
@import "modal";
@import "./layout/header";
@import "./layout/footer";

html {
  scroll-behavior: smooth;
}

body.blocked,
body.lock {
  overflow: hidden;
}

body.overlayed {
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #000;
    opacity: .2;
    z-index: 100;
    cursor: pointer;
  }
}

.container {
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  max-width: 1296px;
  @media (max-width: 1376px) {
    max-width: 100%;
    padding: 0 40px;
  }
  @media (max-width: 768px) {
    max-width: 688px;
    padding: 0;
  }
  @media (max-width: 718.92px) {
    max-width: 100%;
    padding: 0 15px;
  }
}

body {
  font-family: "Inter", sans-serif;
  color: #fff;
  font-size: 15px;
  line-height: 20px;
  font-weight: 300;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #121212;
}

main {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  //margin-top: 86px;
  @media (max-width: $mobileMedium) {
    //margin-top: 60px;
  }
}

.swipe-effect {
  transition: transform 5s ease; /* Пример анимации */
  transform: scale(1.1);
}

.swiper-slide-active {
  opacity: 1 !important;
  z-index: 100 !important;
}

.still-visible {
  z-index: 101 !important;
  opacity: 1 !important;
}

.welcome-slider {
  background-color: #1a1a1a;
  @media (max-width: $mobileMedium) {
    background-color: #121212;
  }

  &__navigation {
    position: absolute;
    display: flex;
    flex-direction: column;
    top: 50%;
    transform: translateY(-50%);
    z-index: 5;
    padding-left: 40px;
    @media (max-width: 1440px) {
      padding-left: 20px;
    }
    @media (max-width: $mobileMedium) {
      display: none;
    }

    &::before {
      content: '';
      position: absolute;
      display: inline-block;
      width: 328px;
      height: 328px;
      border: 5px dotted #a29c9c;
      z-index: 2;
      border-radius: 50%;
      top: 50%;
      transform: translateY(-50%) translateX(calc(-50% - 40px));
      @media (max-width: 1440px) {
        width: 160px;
        height: 160px;
        transform: translateY(-50%) translateX(calc(-50% - 20px));
      }
    }

    &.circle-animation {
      &::before {
        animation: circleAnimation 1s ease-in forwards;
        @media (max-width: 1440px) {
          animation: circleAnimationMedium 1s ease-in forwards;
        }
      }
    }
  }

  &__pagination {
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    font-size: 12px;
    font-weight: 500;
    line-height: 12px;
    color: #9a9090;
    width: 34px;


    .swiper-pagination-current {
      font-size: 18px;
      font-weight: 500;
      line-height: 18px;
      color: #fff;
    }
  }

  &__swiper {
    position: relative;
    height: calc(100vh);
    min-height: 500px;
    @media (max-width: $mobileMedium) {
      height: auto;
    }
  }

  &__prev,
  &__next {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 2;
    margin: 10px 0;
    cursor: pointer;
  }

  &__next {
    transform: rotate(180deg);
  }
}

@keyframes expandWidth {
  0% {
    width: calc(40vw + 80px);
  }
  100% {
    width: 100vw;
  }
}

@keyframes expandWidthMedium {
  0% {
    width: 540px;
  }
  100% {
    width: 100vw;
  }
}

@keyframes expandWidthSmall {
  0% {
    width: 380px;
  }
  100% {
    width: 100vw;
  }
}


@keyframes shrinkWidth {
  0% {
    width: 100vw;
  }
  100% {
    width: calc(40vw + 80px);
  }
}

@keyframes shrinkWidthMedium {
  0% {
    width: 100vw;
  }
  100% {
    width: 540px;
  }
}

@keyframes shrinkWidthSmall {
  0% {
    width: 100vw;
  }
  100% {
    width: 380px;
  }
}

@keyframes shrinkWidth2 {
  0% {
    z-index: 1;
    width: 100vw;
  }
  100% {
    width: calc(40vw + 80px);
    z-index: 0;
  }
}

@keyframes shrinkWidth2Medium {
  0% {
    z-index: 1;
    width: 100vw;
  }
  100% {
    width: 540px;
    z-index: 0;
  }
}

@keyframes shrinkWidth2Small {
  0% {
    z-index: 1;
    width: 100vw;
  }
  100% {
    width: 380px;
    z-index: 0;
  }
}


@keyframes shrinkHeight {
  0% {
    top: 100%;
  }
  100% {
    top: 0;
  }
}


@keyframes expandHeight {
  0% {
    top: 0;
  }
  100% {
    top: 100%;
  }
}

@keyframes circleAnimation {
  0% {
    transform: translateY(-50%) translateX(calc(-50% - 40px)) rotate(0);
  }
  100% {
    transform: translateY(-50%) translateX(calc(-50% - 40px)) rotate(-25.2deg);
  }
}

@keyframes circleAnimationMedium {
  0% {
    transform: translateY(-50%) translateX(calc(-50% - 20px)) rotate(0);
  }
  100% {
    transform: translateY(-50%) translateX(calc(-50% - 20px)) rotate(-23deg);
  }
}


.welcome-item {
  position: relative;
  display: flex;
  background-color: #1a1a1a;

  @media (max-width: $mobileMedium) {
    flex-wrap: wrap;
    background-color: #121212;
  }

  &.still-visible {
    &::before {
      animation: expandWidth 0.6s ease-in forwards;
      @media (max-width: 1440px) {
        animation: expandWidthMedium 0.6s ease-in forwards;
      }
      @media (max-width: $tablet) {
        animation: expandWidthSmall 0.6s ease-in forwards;
      }
    }

    .welcome-item__content {
      &::before {
        animation: shrinkHeight 0.6s ease-in forwards;
      }
    }
  }

  &.animate-back {
    &::before {
      animation: shrinkWidth 0.6s ease-out forwards;
      @media (max-width: 1440px) {
        animation: shrinkWidthMedium 0.6s ease-out forwards;
      }
      @media (max-width: $tablet) {
        animation: shrinkWidthSmall 0.6s ease-out forwards;
      }
    }

    &::after {
      animation: shrinkWidth2 0.8s ease-out forwards;
      @media (max-width: 1440px) {
        animation: shrinkWidth2Medium 0.8s ease-out forwards;
      }
      @media (max-width: $tablet) {
        animation: shrinkWidth2Small 0.8s ease-out forwards;
      }
    }

    .welcome-item__content {
      &::before {
        animation: expandHeight 0.5s ease-in forwards;
      }
    }
  }

  &__subtitle {
    font-size: 24px;
    font-weight: 900;
    line-height: 29.05px;
    margin-bottom: 32px;
    @media (max-width: $mobileMedium) {
      font-size: 18px;
      font-weight: 900;
      line-height: 21.78px;
      margin-bottom: 16px;
    }
  }

  &__description {
    font-size: 15px;
    font-weight: 300;
    line-height: 20px;
    margin-bottom: 32px;
    @media (max-width: $mobileMedium) {
      font-size: 14px;
      font-weight: 300;
      line-height: 20px;
      margin-bottom: 16px;
    }
  }

  &__content {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      left: -10px;
      bottom: 0;
      right: 0;
      top: 100%;
      width: calc(100% + 10px);
      height: calc(100% + 10px);
      background-color: #1a1a1a;
      z-index: 5;
      @media (max-width: $tablet) {
        content: none;
      }
    }
  }

  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: calc(100vw);
    height: 100%;
    background-color: #3e3e3e;
    z-index: -1;
    @media (max-width: $mobileMedium) {
      content: none;
    }
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: calc(40vw + 80px);
    height: 100%;
    background-color: #1a1a1a;
    z-index: 2;
    flex-shrink: 0;
    @media (max-width: 1440px) {
      width: calc(540px);
    }
    @media (max-width: $tablet) {
      width: 380px;
    }
    @media (max-width: $mobileMedium) {
      content: none;
    }
  }

  &__picture {
    position: relative;
    width: calc(100vw - 40vw - 80px);
    flex-grow: 1;
    @media (max-width: 1440px) {
      width: calc(100vw - 500px - 40px);
    }
    @media (max-width: $tablet) {
      width: calc(100vw - 380px);
    }
    @media (max-width: $mobileMedium) {
      width: 100%;
      height: 80vw;
    }

    &::after {
      content: '';
      background: linear-gradient(180deg, rgba(0, 0, 0, .2) 0%, rgba(0, 0, 0, 0) 100%);
      display: block;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__info {
    position: relative;
    width: 40vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    margin-right: 60px;
    z-index: 2;
    padding: 80px 0;
    padding-left: calc(50vw - 648px);
    @media (max-width: 1650px) {
      padding-left: 200px;
    }
    @media (max-width: 1440px) {
      padding-left: 100px;
      margin-right: 40px;
      width: 500px;
    }
    @media (max-width: $tablet) {
      width: 340px;
    }
    @media (max-width: $mobileMedium) {
      width: 100%;
      order: 2;
      padding: 32px 15px;
    }
  }

  &__title {
    position: relative;
    font-size: 48px;
    font-weight: 700;
    line-height: 58.09px;
    @media (max-width: $mobile) {
      font-size: 32px;
      font-weight: 700;
      line-height: 38.73px;
    }


    span {
      font-family: "Poppins";
      color: inherit;
      -webkit-text-fill-color: transparent;
      -webkit-text-stroke: 1px;
    }
  }

  &__btn {
    a {
      position: relative;
      display: flex;
      align-items: center;
      font-size: 12px;
      font-weight: 500;
      line-height: 12px;
      color: $red;
      text-transform: uppercase;
      padding-left: 40px;

      &:hover {
        color: #fff;

        &::before {
          letter-spacing: 0.5em;
          opacity: 0.3;
        }
      }

      &::before {
        content: '.....';
        position: absolute;
        letter-spacing: .3em;
        top: -3px;
        right: 100%;
        margin-right: -30px;
        -webkit-transition: 400ms ease-in-out;
        -o-transition: 400ms ease-in-out;
        transition: 400ms ease-in-out;
        transition-property: all;
        -webkit-transition-property: letter-spacing, opacity;
        -o-transition-property: letter-spacing, opacity;
        transition-property: letter-spacing, opacity;
      }
    }
  }

  &__title,
  &__subtitle,
  &__description,
  &__btn {
    @media (max-width: $tablet) {
      margin-right: -200px;
    }
    @media (max-width: $mobileMedium) {
      margin-right: 0;
    }
  }
}

