.video-item {
  position: relative;
  background-color: #121212;
  padding-left: 30px;
  @media (max-width: $mobile) {
    padding-left: 0;
    height: 60vw;
  }

  &::after {
    content: '';
    background: linear-gradient(180deg, rgba(0, 0, 0, .2) 0%, rgba(0, 0, 0, 0) 100%);
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    display: inline-block;
    opacity: 1;
    transition: opacity .3s;
  }

  &:hover {
    cursor: pointer;

    &::after {
      opacity: 0;
    }

    .video-item__play {
      &::before {
        transform: rotate(90deg);
      }
    }
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__info {
    position: absolute;
    transform: rotate(-90deg);
    transform-origin: left bottom;
    left: 25px;
    bottom: 0;
    font-size: 24px;
    font-weight: 700;
    line-height: 24px;
    @media (max-width: $tablet) {
      font-size: 20px;
      line-height: 22px;
    }
    @media (max-width: $mobile) {
      position: relative;
      display: inline-flex;
      align-items: center;
      transform: none;
      margin-top: 16px;
      font-size: 18px;
      line-height: 24px;
      left: 0;
    }

    span {
      position: relative;
      display: inline-flex;
      align-items: center;
      color: #DC493A;
      margin-right: 20px;
      @media (max-width: $mobile) {
        margin-right: 8px;
      }

      &::before {
        content: '.....';
        margin-right: 14px;
        position: relative;
        top: -5px;
        @media (max-width: $mobile) {
          margin-right: 8px;
        }
      }
    }
  }

  &__play {
    position: absolute;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @media (max-width: $mobile) {
      width: 54px;
      height: 54px;
      svg {
        width: 10px;
        height: 10px;
      }
    }

    &::before {
      content: '';
      position: absolute;
      display: inline-block;
      width: 140px;
      height: 140px;
      border: 1.81px dashed #FFFFFF;
      border-radius: 50%;
      transition: transform .5s;
    }
  }
}

.video-slider {
  position: relative;

  .video-item {
    img {
      width: 100%;
      height: calc(100% - 40px);
      object-fit: cover;
    }

    &__play {
      top: calc(50% - 20px);
    }
  }

  &__pagination {
