  &__next {
    @media (max-width: $mobile) {
      top: calc(100% - 24px);
    }
  }

  &__prev-block {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2;
    transform: translate(-50%, -50%);
    @media (max-width: $mobile) {
      position: static;
      transform: none;
    }
  }

  &__next-block {
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 1;
    transform: translate(50%, 50%);
    @media (max-width: $mobile) {
    }
