.footer {
  background-color: #1A1A1A;
  color: #BABABA;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;

  &__bottom {
    padding: 14px 0;
    color: #7C7C7C;
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
  }

  &__subtext {
    font-size: 15px;
    font-weight: 300;
    line-height: 20px;
  }

  &__logo {
    margin-bottom: 34px;
  }

  &__bottom-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: -5px;
    text-align: center;
    @media (max-width: $mobileMedium) {
      flex-direction: column;
    }
  }

  &__copy {
    display: inline-block;
    margin: 5px;
  }

  ul, li {
    list-style-type: none;
  }

  &__title {
    width: 100%;
    font-size: 24px;
    font-weight: 700;
    line-height: 29.05px;
    margin-bottom: 34px;
    color: #fff;
    @media (max-width: $tablet) {
      margin-bottom: 24px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 16px;
    }
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    padding: 110px 0 80px;
    margin: 0 -40px;
    @media (max-width: $tablet) {
      flex-wrap: wrap;
      margin: 0 -20px;
      padding: 90px 0 60px;
    }
    @media (max-width: $mobile) {
      padding: 48px 0 50px;
    }
    @media (max-width: $mobileMedium) {
      margin:0 0 -40px;
    }
  }

  &__nav {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    a {
      display: block;
      padding: 5px;
      @include hover-effect;
    }
  }

  &__inner-col {
    width: calc(50% - 10px);
    margin: -5px;
  }

  &__contacts {
    display: flex;
    flex-direction: column;

    .footer-contact + .footer-contact {
      margin-top: 16px;
    }
  }

  &__logo-block,
  &__nav,
  &__contacts-block {
    width: 320px;
    margin: 0 40px;
    max-width: calc((100% / 3) - 80px);
    @media (max-width: $tablet) {
      width: calc(50% - 40px);
      max-width: unset;
      margin: 0 20px;
    }
    @media (max-width: $mobileMedium) {
      max-width: 100%;
      width: 100%;
      margin:0 0 40px;
    }
  }

  &__nav {
    @media (max-width: $tablet) {
      max-width: 100%;
      width: 100%;
      order: 2;
      margin-top: 40px;
    }
    @media (max-width: $mobileMedium) {
      margin-top: 0;
    }
  }
}

a.footer-contact {
  @include hover-effect;
  white-space: nowrap;
}

.footer-contact {
  display: flex;
  align-items: center;

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 25px;
    @media (max-width: $tablet) {
      margin-right: 15px;
    }
  }
}